      ]
    }
  },
  computed: {
    userData:function(){
      return this.$store.getters.userData
    }
  },
  methods: {
    color (value) {
      let $color
      if (value <= 25) {
        $color = 'info'
      } else if (value > 25 && value <= 50) {
        $color = 'success'
      } else if (value > 50 && value <= 75) {
        $color = 'warning'
      } else if (value > 75 && value <= 100) {
        $color = 'danger'
      }
      return $color
    }
  },
  created() {
    let userData = JSON.parse(localStorage.user)
    checkPO(this)

    getDatas(this, localStorage.base_api + 'karyawan/' + userData.id_karyawan, {method:'POST', headers:{'Authorization': 'bearer ' + localStorage.token}}, 'post')
    .catch(e => {
      if(e.response.status == 401) {
        this.$swal('Session Login kamu sudah habis!', 'Login lagi yah...', 'warning')
        this.$store.dispatch('logout')
        .then(() => {
          setTimeout(() => {
            this.$swal.close()
            this.$router.replace('/login')
          }, 1500)  
        })
        .catch(() => console.error(e))
      }
      else {
        this.$swal('Tidak bisa mengambil data!', 'Mohon Hubungi pengembangnya...', 'error')
        setTimeout(() => {
          this.$swal.close()
        }, 1500)
        return false
      }
    })
  }
